.container{
    width: 100%;
}
.top{
    height: 40px;
    width: 100%;
    border: none;
    background-color: rgb(248, 132, 0);
    position: relative;
}
.top > img{
    width: 30px;
    height: auto;
    position: absolute;
    top: 12%;
    left: 1%;
}
input::-webkit-input-placeholder{
    color: white;
}
.top-search{
    width: 85%;
    height: 50%;
    background-color: orangered;
    border: none;
    border-radius: 5px;
    text-align: center;
    position: absolute;
    top: 20%;
    right: 3%;
}
.context{
    width: 100%;
    height: 1500px;
    padding-top: 10px;
    background-color: rgb(230, 229, 229);
    position: relative;
}
.A-list{
    width: 100%;
    height: auto;
    margin-top: 10px;
}
.A-list div{
    width: 42%;
    height: auto;
    background-color: white;
    float: left;
    border-radius: 10px;
    overflow: hidden;
    margin-left: 5.3%;
    margin-bottom: 5%;
}
.p-text{
    padding-left: 2%;
    position: relative;
}
.p-cost{
    padding-left: 40px;
    position: relative;
}
.p-cost span{
    color: orangered;
    font-size: 15px;
    position: absolute;
    left: 2%;
}
.A-list img{
    width: 100%;
    height: auto;
}