.container{
    text-align: center;
}
.go-back{
    background: rgba(0, 0, 0, 40%);
    text-align: center;
    font-size: x-large;
    border-radius: 50%;
    color: white;
    width: 30px;
    height: 30px;
    position: absolute;
    top: 3%;
    left: 5%;
}
img{
    width: 100%;
    height: auto;
}

.tab-bottom{
    width: 100%;
    height: 50px;
    border-top: 1px solid rgb(206, 200, 200);
}

.tl{
    float: left;
    padding-left: 11px;
    margin-top: 2%;
}
#cstar{
    background: url('./img/star1.png') ;
    /* background: iconfont icon-favorite; */
    appearance: none;
}
.choose-two{
    width: 60%;
    height: 35px;
    border-radius: 17.5px;
    overflow: hidden;
    font-weight: bold;
    position: absolute;
    right: 10px;
    top: 10%;
}
.cl{
    width: 50%;
    height: 35px;
    padding-top: 9px;
}
.cr{
    width: 50%;
